.glow {
    margin: 0;
    width: 100%;
    height: 0.2rem;
    background: rgb(0,255,255);
background: linear-gradient(90deg, hsl(180, 100%, 50%) 0%, rgba(255,0,255,1) 100%);
    animation-name: bg;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-direction: alternate;
    animation-iteration-count: infinite;
  }
  @keyframes bg {
    0% {
        background: rgb(0,255,255);
background: linear-gradient(90deg, rgba(0,255,255,1) 0%, hsla(0, 0%, 100%, 0.545) 0%, rgba(255,0,255,1) 100%);
      }
      12.5%{
        background: rgb(0,255,255);
        background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.54) 12.5%, rgba(255,0,255,1) 100%);
      }
      20% {
        background: rgb(0,255,255);
background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.5438550420168067) 25%, rgba(255,0,255,1) 100%);
      }
      32.5%{
        background: rgb(0,255,255);
        background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.54) 32.5%, rgba(255,0,255,1) 100%);
      }
      50% {
        background: rgb(0,255,255);
background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.5438550420168067) 50%, rgba(255,0,255,1) 100%);
box-shadow: 0px 30px 82px 10px rgba(15, 255, 255, 0.103);
      }
      62.5%{
        background: rgb(0,255,255);
        background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.54) 62.5%, rgba(255,0,255,1) 100%);
      }
      75%{
        background: rgb(0,255,255);
        background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.5438550420168067) 75%, rgba(255,0,255,1) 100%);
      }
      87.5%{
        background: rgb(0,255,255);
        background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.54) 87.5%, rgba(255,0,255,1) 100%);
      }
      100% {
        background: rgb(0,255,255);
background: linear-gradient(90deg, rgba(0,255,255,1) 0%, rgba(255,255,255,0.5438550420168067) 100%, rgba(255,0,255,1) 100%);
      }
  }
