@import url('https://fonts.googleapis.com/css2?family=Kaisei+Opti:wght@700&display=swap');
.navbar{
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 3.2rem;
    -webkit-transition: all 1s linear;
}
/*search field*/
.searchnav>input{
    background: 0;
    border: 0 !important;
    outline: none;
}
.searchnav:focus-within + .line:after {
    transform: scaleX(1);
}
.searchnav>button{

    border-left: 0px solid rgb(221, 221, 221) !important;
    background-color: transparent !important;

}
.react-search-field-button svg{
    fill: dodgerblue !important;
}
.searchnav {
    margin-top: 2vh;
    margin-right: 2vw;
    border: 0px solid rgb(221, 221, 221) !important;
    width: 20%;
}
.put{
    float: right;
}
/*search line*/
.line{
    width: 90%;
    height: 3px;
    bottom: -8px;
    background: dodgerblue;
}
.searchnav:focus-within + .line:after {
    transform: scaleX(1);
}
.line::after  {
    content: "";
    float: right;
    width: 100%;
    height: 3px;
    transform: scalex(0);
    transition: 0.3s ease;
    background: linear-gradient(to right, #17e9e0, #a64ac9);
}
/*links*/
.links{
    margin-left: 30vw;
    padding-top: 0.8rem;
    position: absolute;
}
@media screen and (max-width: 900px){
    .links{
        margin-left: 25vw;
    }
}
.links a{
    color: dodgerblue;
    text-decoration: none;
    padding: 1.3vw;
    font-family: 'Kaisei Opti', serif;
}
.links a:hover{
    color: black;
}
/*logo*/
.logo{
    width: auto;
    height: 3.3rem;
    margin-top: -0.1vh;
    margin-left: 5vw;
    color: dodgerblue;
    animation-name: logo;
    animation-duration: 9s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@keyframes logo {
    0%{transform: rotate(0deg);}
    50%{transform: rotate(180deg);}
    100%{transform: rotate(360deg);}
}
.strong{
    font-size: 1.8rem;
}
.logotxt{
    color: dodgerblue;
    font-size: 1.5rem;
    position: relative;
    top: -3.2rem;
    left: 6.5rem;
}
@media screen and (max-width: 790px){
    .logotxt{
        position: absolute;
        top: 2rem;
    }
    .links{
        display: none;
    }
}
@media screen and (max-width: 660px){
    .logotxt{
        position: relative;
        top: -3.2rem;
        font-size: 1.5rem;
    }
}
@media screen and (max-width: 460px){
    .put{
        display: none;
    }
    .logotxt{
        font-size: 1.7rem;
        line-height: 2.5rem;
    }
    .strong{
        font-size: 2.5rem;
    }
}
