.slider{
    height: auto;
}
@media screen and (min-width: 768px){
    .slider{
        height: 23rem;
    }
}
/*size of slider on each-slide effect*/
.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 22rem;
  }