body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



html {
  font-size: calc(16px + (26 - 14) * ((100vw - 200px) / (2560 - 200)));

  box-sizing: border-box;      
}  

*,
*::before, 
*::after {
  margin: 0; 
  padding: 0;
  box-sizing: inherit;
 
}


html {
  scroll-behaviour: smooth;
 }



:root{
  --color: rgb(255, 255, 255);
  --text-shadow: rgb(0, 0, 0) 0px 2px 0px;
}
body{
  background-color: rgb(255, 255, 255) !important;
}
.text{
  color: rgb(255, 255, 255);
  color: var(--color);
  font-weight: 600;
  background-color: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
  border-radius: 15px 15px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  position: absolute;
  top: 10rem;
  left: 7rem;
  z-index: 1;
  width: 16rem;
  padding: 10px 20px;
  text-shadow: rgb(0, 0, 0) 0px 2px 0px;
  text-shadow: var(--text-shadow);
  overflow: hidden;
  -webkit-animation-name: bounceInLeft1;
  animation-name: bounceInLeft1;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-delay: 0s !important;
          animation-delay: 0s !important;
}
.light{
  background: #0ff;
  box-shadow: 0 0 5px #0ff, 0 0 15px #0ff, 0 0 30px #2bd2ff,
    0 0 60px #0ff;
    width: 10%;
    height: 0.3rem;
    border-radius: 0px 0px 20px 20px;
    margin-top: -0.5rem;
    bottom: 0px;
}
@media screen and (max-width:768px){
  .text{
    display: none;
  }
}
@media screen and (max-width:587px){
  .text{
    display: none;
  }

}
@-webkit-keyframes bounceInLeft1 {

  0%,
  60%,
  75%,
  90%,
  to {
      -webkit-animation-timing-function: cubic-bezier(.215, .61, .355, 1);
      animation-timing-function: cubic-bezier(.215, .61, .355, 1)
  }

  0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0)
  }

  60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0)
  }

  75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0)
  }

  90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0)
  }

  to {
      -webkit-transform: none;
      transform: none
  }
}
@keyframes bounceInLeft1 {

  0%,
  60%,
  75%,
  90%,
  to {
      -webkit-animation-timing-function: cubic-bezier(.215, .61, .355, 1);
      animation-timing-function: cubic-bezier(.215, .61, .355, 1)
  }

  0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0)
  }

  60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0)
  }

  75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0)
  }

  90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0)
  }

  to {
      -webkit-transform: none;
      transform: none
  }
}
/*tab bar*/
.tab-bar{
  z-index: 100;
}
.tab-bar2{
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}
.home{
  color: rgb(0, 0, 0);
  box-shadow: 0px 10px 39px 10px rgba(0,0,0,0.24);
  -webkit-transition: all 1s linear;
}
.home:hover{
  color: black;
  box-shadow: 0px -10px 132px 23px #0ff;
  background: rgb(0,255,255);
background: linear-gradient(0deg, rgba(0, 255, 255, 0.315) 0%, rgb(0, 255, 255) 100%);
}

 
 
 
 
 
.navbar{
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 3.2rem;
    -webkit-transition: all 1s linear;
}
/*search field*/
.searchnav>input{
    background: 0;
    border: 0 !important;
    outline: none;
}
.searchnav:focus-within + .line:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
.searchnav>button{

    border-left: 0px solid rgb(221, 221, 221) !important;
    background-color: transparent !important;

}
.react-search-field-button svg{
    fill: dodgerblue !important;
}
.searchnav {
    margin-top: 2vh;
    margin-right: 2vw;
    border: 0px solid rgb(221, 221, 221) !important;
    width: 20%;
}
.put{
    float: right;
}
/*search line*/
.line{
    width: 90%;
    height: 3px;
    bottom: -8px;
    background: dodgerblue;
}
.searchnav:focus-within + .line:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
.line::after  {
    content: "";
    float: right;
    width: 100%;
    height: 3px;
    -webkit-transform: scalex(0);
            transform: scalex(0);
    transition: 0.3s ease;
    background: linear-gradient(to right, #17e9e0, #a64ac9);
}
/*links*/
.links{
    margin-left: 30vw;
    padding-top: 0.8rem;
    position: absolute;
}
@media screen and (max-width: 900px){
    .links{
        margin-left: 25vw;
    }
}
.links a{
    color: dodgerblue;
    text-decoration: none;
    padding: 1.3vw;
    font-family: 'Kaisei Opti', serif;
}
.links a:hover{
    color: black;
}
/*logo*/
.logo{
    width: auto;
    height: 3.3rem;
    margin-top: -0.1vh;
    margin-left: 5vw;
    color: dodgerblue;
    -webkit-animation-name: logo;
            animation-name: logo;
    -webkit-animation-duration: 9s;
            animation-duration: 9s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
@-webkit-keyframes logo {
    0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    50%{-webkit-transform: rotate(180deg);transform: rotate(180deg);}
    100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@keyframes logo {
    0%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    50%{-webkit-transform: rotate(180deg);transform: rotate(180deg);}
    100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
.strong{
    font-size: 1.8rem;
}
.logotxt{
    color: dodgerblue;
    font-size: 1.5rem;
    position: relative;
    top: -3.2rem;
    left: 6.5rem;
}
@media screen and (max-width: 790px){
    .logotxt{
        position: absolute;
        top: 2rem;
    }
    .links{
        display: none;
    }
}
@media screen and (max-width: 660px){
    .logotxt{
        position: relative;
        top: -3.2rem;
        font-size: 1.5rem;
    }
}
@media screen and (max-width: 460px){
    .put{
        display: none;
    }
    .logotxt{
        font-size: 1.7rem;
        line-height: 2.5rem;
    }
    .strong{
        font-size: 2.5rem;
    }
}
