/*input*/
.sign-input{
    width: 50%;
    outline: none;
    background-color:rgba(0, 0, 0, 0);
    border: 0;
    font-size: 1.3rem;
    color: rgb(255, 255, 255);
    height: 8vh;
    margin-top: 4vh;
    text-shadow: 0px 5px 0px rgba(255, 255, 255, 0.486);
}
.sign-input::-webkit-input-placeholder{
    color: white;
    text-shadow: 0px 5px 0px rgba(255, 255, 255, 0.486);
}
.sign-input:-ms-input-placeholder{
    color: white;
    text-shadow: 0px 5px 0px rgba(255, 255, 255, 0.486);
}
.sign-input::placeholder{
    color: white;
    text-shadow: 0px 5px 0px rgba(255, 255, 255, 0.486);
}
/*search line*/
.lineform{
    width: 50%;
    height: 4px;
    border-radius: 20px;
    bottom: -8px;
    background: #ffffff;
    box-shadow: 0 5px 0px 0px rgba(255, 255, 255, 0.411);
} 
.lineform:after{
    content: "";
  
    float: right;
    width: 100%;
    height: 4px;
    -webkit-transform: scalex(0);
            transform: scalex(0);
    transition: 0.3s ease;
    background: linear-gradient(to right, #0ff, #f0f);
    box-shadow: 0 -25px 30px 10px rgba(255, 255, 255, 0);
}
input:focus + .lineform:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
/*button*/
.sign-button{
    background-color: #1f8fff !important;
    color: white;
    margin-top: 4.5rem;
    border-radius: 40px 40px !important;
    box-shadow: 0px 9px 0px 0px rgb(30, 143, 255);
}
.sign-button:hover{
    box-shadow: 0px 5px 19px 6px rgba(30,143,255,1);
}
@media screen and (max-width:500px){
    .sign-input,
    .lineform{
        width: 90%;
    }
}
.in{
    z-index: 100 !important;
}

