.marquee__inner {
    display: flex;
}
.marquee__inner h1 {
    padding-right: 30px;
    font-size: 5.5vw;
    white-space: nowrap;
    -webkit-animation: kf-marquee 60s linear infinite;
            animation: kf-marquee 60s linear infinite;
    text-shadow: -1px -1px 0 #ccc, 1px -1px 0 #ccc, -1px 1px 0 #ccc, 1px 1px 0 #ccc;
    color: #141313;
    transition: color 0.3s linear;
}
.marquee__inner h1:hover {
    color: #fcfcfc;
}
@-webkit-keyframes kf-marquee {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
   }
    100% {
        -webkit-transform: translate(-100%, 0);
                transform: translate(-100%, 0);
   }
}
@keyframes kf-marquee {
    0% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
   }
    100% {
        -webkit-transform: translate(-100%, 0);
                transform: translate(-100%, 0);
   }
}


@media screen and (min-width: 769px){
    .marquee__inner h1{
        display: none;
    }
}
@media screen and (max-width: 500px){
    .marquee__inner h1{
        font-size: 5vw;
        height: 7vw;
    }
    .marquee div{
        -webkit-animation: marquee 4s linear infinite;
                animation: marquee 4s linear infinite;
    }
}
@media screen and (max-width: 370px){
    .marquee__inner h1{
        font-size: 8vw;
        height: 11vw;
    }
    .marquee div{
        -webkit-animation: marquee 2.5s linear infinite;
                animation: marquee 2.5s linear infinite;
    }
}
