.map{
    border-radius: 10px 10px;
    width: 80%; 
    animation-name: frame;
    animation-duration: 20s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    box-shadow: 0px 36px 39px 0px rgba(0,0,0,0.31);
    animation-direction: alternate;
}
@keyframes frame{
    0%{border: 2px solid rgb(255, 0, 255);
        
        
    }
    50%{}
    100%{border: 2px solid dodgerblue;
    }
}
.cir{
    position: absolute;
    left: 7rem;
    margin-top: -3rem;
}
@media screen and (max-width:550px){
    .cir{
        margin-left: -4rem;
    }
}
.rent{
    float: right;
    margin-top: -7rem;
    margin-right: 7rem;
    background-color: 0;
    backdrop-filter: blur(30px);
    border-radius: 10px 10px;
}
@media screen and (max-width:1000px){
    .rent{
        margin-right: 5rem;
    }
}
@media screen and (max-width:1000px){
    .rent{
        display: none;
    }
}